.wp-block-tails2tell-slider-members {
	border: 1px dotted #f00;
	justify-content: flex-start;

	.card-container{

		.swiper-wrapper{
			flex-wrap: wrap;
			gap: 3rem;
		}

		.swiper-button-prev{
			display: none;
		}

		.swiper-button-next{
			@extend .swiper-button-prev
		}
	}
	

	.card-wrapper{
		align-items: center;
		width: 18rem !important;


		button{
			aspect-ratio: 1;
			animation: 0.2s ease-in-out scale-out;
			background-color: #fff;
			border-radius: 50%;
			border: dashed 2px #007cba;
			color: #007cba;
			height: 100%;
			justify-content: center;
			padding: 0;
			width: 90% !important;
			
			&:hover{
				animation: 0.2s ease-in-out scale;
				transform: scale(1.1);
			}
			
			p{	
				font-size: large;
				font-weight: 600;
				max-width: 130px;
			}

			img{
				width: -webkit-fill-available;
				margin: 0 !important;
			}
		}

	}
}


// Swiper content

@keyframes scale {
	from{
		transform: scale(1);
	}
	to{
		transform: scale(1.1);
	}
}
@keyframes scale-out {
	from{
		transform: scale(1.1);
	}
	to{
		transform: scale(1);
	}
}
